.geodesy {
  flex-grow: 1;
  color: #ffffff;
  background-image: url("../img/geodesy-bg.png");
  background-repeat: no-repeat;
  background-position: left;
  background-size: cover;
  padding: 160px 0 90px;
  &__desc {
    max-width: 840px;
    margin-top: 75px;
    margin-left: auto;
  }
  &__subtitle {
    font-family: 'GrtskGiga';
    font-size: 16px;
    line-height: 140%;
    font-weight: 700;
  }
  &__text {
    margin-top: 30px;
    p {
      font-size: 16px;
      line-height: 140%;
    }
    p:not(:first-child) {
      margin-top: 20px;
    }
  }
  &__list {
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
    margin-top: 30px;
    li {
      display: flex;
      align-items: center;
      i {
        flex-shrink: 0;
        width: 20px;
        height: 20px;
        display: flex;
        justify-content: center;
        align-items: center;
        background: #2196F3;
        border-radius: 50%;
      }
      i img {
        width: 11px;
      }
      p {
        font-size: 16px;
        line-height: 140%;
        margin-left: 12px;
      }
    }
  }
  .nav {
    margin-top: 100px;
  }
}

@media screen and (max-width: 992px) {
  .geodesy {
    padding: 100px 0 50px;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.4);
    }
    &__inner {
      position: relative;
      z-index: 2;
    }
    &__desc {
      margin-top: 60px;
      margin-left: 0;
    }
  }
}

@media screen and (max-width: 576px) {
  .geodesy {
    &__desc {
      margin-top: 30px;
    }
    .nav {
      margin-top: 50px;
    }
  }
}

@media screen and (max-width: 360px) {
  .geodesy {
    &__subtitle {
      font-size: 14px;
    }
    &__list {
      margin-top: 15px;
      li {
        p {
          font-size: 14px;
        }
      }
    }
    &__text {
      margin-top: 15px;
      p {
        font-size: 14px;
      }
    }
  }
}