.main {
  flex-grow: 1;
  color: #ffffff;
  background-image: url("../img/main-bg.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding: 160px 0 90px;
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
  }
  &__left {
    max-width: 770px;
    margin-right: 50px;
  }
  &__text {
    max-width: 485px;
    margin: 25px 0 0 auto;
    p {
      font-size: 16px;
      line-height: 140%;
    }
  }
  &__images {
    position: relative;
    margin-right: 50px;
    margin-bottom: 25px;
  }
  &__img {
    position: relative;
    z-index: 1;
    width: 220px;
    img {
      width: 100%;
    }
  }
  .nav {
    margin-top: 340px;
  }
}

@media screen and (max-width: 1200px) {
  .main {
    &__images {
      margin: 0;
    }
    &__img2 {
      display: none;
    }
    .nav {
      margin-top: 150px;
    }
  }
}

@media screen and (max-width: 992px) {
  .main {
    padding: 100px 0 50px;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.4);
    }
    &__inner {
      position: relative;
      z-index: 2;
    }
    &__wrap {
      display: block;
    }
    &__left {
      max-width: 100%;
      margin: 0 0 50px 0;
    }
    &__text {
      margin-left: 0;
    }
    &__img {
      width: 150px;
    }
    .nav {
      margin-top: 100px;
    }
  }
}

@media screen and (max-width: 576px) {
  .main {
    &__title {
      font-size: 28px;
    }
    .nav {
      margin-top: 50px;
    }
  }
}

@media screen and (max-width: 360px) {
  .main {
    &__left {
      margin-bottom: 25px;
    }
    &__text {
      margin-top: 15px;
      p {
        font-size: 14px;
      }
    }
  }
}