.contacts {
  flex-grow: 1;
  color: #ffffff;
  background-image: url("../img/contacts-bg.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding: 160px 0 90px;
  &__desc {
    max-width: 530px;
    margin-left: auto;
    margin-top: 50px;
  }
  &__subtitle {
    font-size: 36px;
    line-height: 140%;
    font-weight: 600;
  }
  &__items {
    display: inline-flex;
    flex-direction: column;
    margin-top: 25px;
  }
  &__item {
    display: inline-flex;
    align-items: center;
    &:not(:first-child) {
      margin-top: 15px;
    }
    &:hover {
      i svg path {
        fill: #B3FEFF;
      }
      span {
        color: #B3FEFF;
      }
    }
    i {
      flex-shrink: 0;
      width: 18px;
    }
    i svg path {
      transition: all 0.3s;
    }
    span {
      font-size: 16px;
      line-height: 140%;
      margin-left: 12px;
      transition: all 0.3s;
    }
  }
  &__btns {
    display: flex;
    align-items: center;
    margin-top: 25px;
  }
  &__btn {
    padding: 5px 55px;
  }
  &__socials {
    display: flex;
    align-items: center;
    margin-left: 18px;
  }
  &__social {
    width: 55px;
    height: 55px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #FFF;
    border-radius: 50%;
    &:not(:first-child) {
      margin-left: 10px;
    }
    img {
      width: 23px;
    }
  }
  .nav {
    margin-top: 230px;
  }
}

@media screen and (max-width: 1200px) {
  .contacts {
    .nav {
      margin-top: 150px;
    }
  }
}

@media screen and (max-width: 992px) {
  .contacts {
    padding: 100px 0 50px;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.4);
    }
    &__inner {
      position: relative;
      z-index: 2;
    }
    &__desc {
      margin: 60px 0 0 0;
    }
    &__subtitle {
      font-size: 24px;
    }
    .nav {
      margin-top: 100px;
    }
  }
}

@media screen and (max-width: 576px) {
  .contacts {
    &__desc {
      margin-top: 30px;
    }
    &__subtitle {
      font-size: 24px;
    }
    &__btns {
      display: block;
    }
    &__socials {
      margin: 15px 0 0 0;
    }
    .nav {
      margin-top: 50px;
    }
  }
}