.vacancy {
  flex-grow: 1;
  color: #ffffff;
  background-image: url("../img/vacancy-bg.png");
  background-repeat: no-repeat;
  background-position: left;
  background-size: cover;
  padding: 160px 0 90px;
  &__desc {
   max-width: 625px;
    margin: 190px auto 0;
  }
  &__subtitle {
    font-size: 36px;
    line-height: 140%;
    font-weight: 600;
    margin-left: 200px;
  }
  &__btn {
    padding: 5px 74px;
    margin-top: 30px;
    margin-left: 200px;
  }
  .nav {
    margin-top: 270px;
  }
}

@media screen and (max-width: 1200px) {
  .vacancy {
    .nav {
      margin-top: 150px;
    }
  }
}

@media screen and (max-width: 992px) {
  .vacancy {
    padding: 100px 0 50px;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.4);
    }
    &__inner {
      position: relative;
      z-index: 2;
    }
    &__desc {
      max-width: 100%;
      margin: 60px 0 0 0;
    }
    &__subtitle {
      font-size: 24px;
      margin-left: 0;
    }
    &__btn {
      margin-top: 15px;
      margin-left: 0;
    }
    .nav {
      margin-top: 100px;
    }
  }
}

@media screen and (max-width: 576px) {
  .vacancy {
    &__desc {
      margin-top: 30px;
    }
    &__subtitle {
      font-size: 20px;
    }
    .nav {
      margin-top: 50px;
    }
  }
}