.wrap {
  position: relative;
  height: 100%;
  overflow-x: hidden;
}
.content {
  display: flex;
  flex-direction: column;
  height: 100%;
}
.nav {
  display: grid;
  grid-template-columns: repeat(6, calc(100% / 6 - 145px / 6));
  gap: 29px;
  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    background: #108CFF;
    border-radius: 15px;
    padding: 30px 5px;
    &-top {
      position: relative;
      margin-bottom: auto;
    }
    &-icon {
      position: relative;
      z-index: 1;
      width: 68px;
      svg {
        width: 100%;
        height: auto;
      }
    }
    &-icon svg path {
      transition: all 0.3s;
    }
    &-icon2 {
      position: absolute;
      left: 25px;
      top: -6px;
      width: 76px;
      svg {
        width: 100%;
        height: auto;
      }
    }
    &-icon2 svg path {
      transition: all 0.3s;
    }
    &-title {
      font-family: 'GrtskGiga';
      font-size: 15px;
      line-height: 120%;
      font-weight: 500;
      margin-top: 15px;
      transition: all 0.3s;
    }
  }
  &__item--active,
  &__item:hover {
    background: #ffffff;
    transform: rotate(-4.2deg);
    .nav__item-icon svg path {
      fill: #007aff;
    }
    .nav__item-icon2 svg path {
      fill: #DCEEFF;
    }
    .nav__item-title {
      color: #1E1E1E;
    }
  }
}
.text64 {
  font-family: 'GrtskGiga';
  font-size: 64px;
  line-height: 120%;
}
.list {
  display: grid;
  grid-template-columns: 100%;
  gap: 15px;
  padding: 50px 0;
  li {
    font-family: 'GrtskGiga';
    font-size: 20px;
    line-height: 120%;
  }
  li a:hover {
    color: #007aff;
  }
}
.btn-blue {
  height: 65px;
  background: #108CFF;
  border-radius: 1000px;
  &:hover {
    background: #127ee7;
  }
  span {
    font-size: 16px;
    line-height: 120%;
    color: #FFF;
  }
}
.menu {
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #ffffff;
  padding: 115px 0 75px;
  overflow-y: auto;
  opacity: 0;
  pointer-events: none;
  transition: all 0.5s;
  &--active {
    opacity: 1;
    pointer-events: all;
  }
  &::-webkit-scrollbar {
    width: 0;
  }
  &__close {
    position: absolute;
    top: 28px;
    right: 26px;
    width: 46px;
    cursor: pointer;
    img {
      width: 100%;
    }
  }
  &__inner {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__logo {
    display: flex;
    align-items: center;
    &-icon {
      flex-shrink: 1;
      width: 40px;
      img {
        width: 100%;
      }
    }
    &-text {
      margin-left: 15px;
      p:nth-child(1) {
        font-size: 14px;
        line-height: 120%;
      }
      p:nth-child(2) {
        font-size: 25px;
        line-height: 120%;
        font-weight: 700;
        span {
          color: #4183a4;
        }
      }
    }
  }
  .nav {
    width: 100%;
    color: #ffffff;
    margin-top: 300px;
    &__item--active,
    &__item:hover {
      background: #F4F4F4;
    }
  }
  &__bottom {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #F4F4F4;
    border-radius: 20px;
    padding: 27px 45px;
    margin-top: 300px;
  }
  &__group {
    display: flex;
    align-items: center;
    &:not(:first-child) {
      margin-left: 30px;
    }
  }
  &__mail {
    display: flex;
    align-items: center;
    &:hover {
      i {
        border: 1px solid #108cff;
      }
      i svg path {
        fill: #108cff;
      }
      span {
        color: #108cff;
      }
    }
    i {
      width: 38px;
      height: 38px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: transparent;
      border: 1px solid #1E1E1E;;
      border-radius: 50%;
      transition: all 0.3s;
      svg {
        width: 14px;
        height: 14px;
      }
      svg path {
        transition: all 0.3s;
      }
    }
    span {
      font-size: 16px;
      line-height: 120%;
      color: #1E1E1E;;
      margin-left: 15px;
      transition: all 0.3s;
    }
  }
  &__numbers {
    display: flex;
    align-items: center;
  }
  &__number {
    font-size: 16px;
    line-height: 120%;
    font-weight: 500;
    color: #1E1E1E;
    &:not(:first-child) {
      margin-left: 15px;
    }
    &:hover {
      color: #108cff;
    }
  }
  &__btns {
    display: flex;
    align-items: center;
    margin-left: 50px;
  }
  &__btn {
    height: 55px;
    padding: 5px 55px;
  }
  &__socials {
    display: flex;
    align-items: center;
    margin-left: 15px;
  }
  &__social {
    width: 55px;
    height: 55px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #F4F4F4;
    border: 1px solid #108CFF;
    border-radius: 50%;
    &:not(:first-child) {
      margin-left: 15px;
    }
    img {
      width: 23px;
    }
  }
}
.modal {
  background: rgba(30, 140, 255, 0.80);
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  overflow-y: auto;
  z-index: 1000;
  opacity: 0;
  pointer-events: none;
  &--active{
    opacity: 1;
    pointer-events: unset;
  }
  @media screen and (min-width: 993px) {
    text-align: center;
    &:before {
      display: inline-block;
      vertical-align: middle;
      content: " ";
      height: 100%;
    }
    &__dialog {
      display: inline-block;
      text-align: left;
      vertical-align: middle;
    }
  }
  &__dialog{
    width: 446px;
    max-width: 100%;
    position: relative;
    background: #FFF;
    border-radius: 20px;
    padding: 50px;
    margin: 0 auto;
    @media screen and (max-width: 992px) {
      margin: 50px auto;
      max-width: calc(100% - 30px);
    }
  }
  &__close{
    position: absolute;
    top: 15px;
    right: 15px;
    width: 18px;
    background-color: transparent;
    padding: 0;
    img {
      width: 100%;
    }
  }
  &__content {
    text-align: center;
  }
  &__title {
    font-size: 26px;
    line-height: 140%;
    font-weight: 600;
    color: #1E1E1E;
  }
  &__subtitle {
    font-size: 15px;
    line-height: 140%;
    color: #1E1E1E;
    margin-top: 15px;
  }
  &__form {
    max-width: 321px;
    display: grid;
    grid-template-columns: 100%;
    gap: 18px;
    margin: 40px auto 0;
    &-group {
      input {
        width: 100%;
      }
    }
    &-btn {
      width: 100%;
      height: 55px;
    }
    &-info {
      max-width: 260px;
      margin: 10px auto 0;
    }
    .file-group {
      position: relative;
      input {
        position: relative;
        z-index: 1;
        opacity: 0;
      }
      span {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        background: #FFF;
        border: 1px dashed #3099FA;
        border-radius: 1000px;
        font-size: 16px;
        line-height: 120%;
        color: #108CFF;
      }
    }
  }
  &__img {
    width: 125px;
    margin: 30px auto 0;
    img {
      width: 100%;
    }
  }
}
.form {
  &-group {
    margin: 0;
    padding: 0;
    border: none;
    input {
      height: 55px;
      background: #FFF;
      border: 1px solid #DCDCDC;
      border-radius: 1000px;
      padding: 3px 30px;
      font-size: 16px;
      line-height: 120%;
      color: #1E1E1E;
      &::placeholder {
        color: #969696;
      }
    }
  }
  &-info {
    font-size: 14px;
    line-height: 140%;
    color: #7B7B7B;
    a {
      color: #108CFF;
    }
  }
}


@media screen and (max-width: 1200px) {
  .nav {
    grid-template-columns: repeat(3, calc(100% / 3 - 58px / 3));
  }
  .text64 {
    font-size: 54px;
  }
  .menu {
    .nav {
      margin-top: 150px;
    }
    &__bottom {
      margin-top: 150px;
    }
    &__numbers {
      flex-direction: column;
    }
    &__number {
      &:not(:first-child) {
        margin: 10px 0 0 0;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .nav {
    &__item {
      &-icon {
        width: 50px;
      }
      &-icon2 {
        width: 60px;
      }
    }
  }
  .text64 {
    font-size: 32px;
  }
  .menu {
    &__close {
      width: 32px;
    }
    .nav {
      margin-top: 100px;
    }
    &__bottom {
      flex-direction: column;
      background: transparent;
      padding: 0;
      margin-top: 100px;
    }
    &__group {
      flex-direction: column;
      &:not(:first-child) {
        margin: 15px 0 0 0;
      }
    }
    &__btns {
      margin: 15px 0 0 0;
    }
  }
}

@media screen and (max-width: 768px) {
  .nav {
    grid-template-columns: repeat(3, calc(100% / 3 - 30px / 3));
    gap: 15px;
    &__item {
      &-title {
        font-size: 14px;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .nav {
    grid-template-columns: repeat(2, calc(50% - 15px / 2));
  }
  .btn-blue {
    height: 50px;
  }
  .menu {
    padding: 100px 0 50px;
    &__logo {
      &-text {
        p:nth-child(2) {
          font-size: 14px;
        }
      }
    }
    .nav {
      margin-top: 50px;
    }
    &__bottom {
      margin-top: 50px;
    }
    &__btns {
      flex-direction: column;
    }
    &__socials {
      margin: 15px 0 0 0;
    }
  }
  .modal {
    &__dialog {
      padding: 50px 25px;
    }
    &__title {
      font-size: 20px;
    }
  }
}

@media screen and (max-width: 360px) {
  .nav {
    &__item {
      &-icon {
        width: 40px;
      }
      &-icon2 {
        left: 15px;
        width: 50px;
      }
      &-title {
        font-size: 12px;
      }
    }
  }
  .text64 {
    font-size: 24px;
  }
}