.header {
  position: absolute;
  z-index: 5;
  top: 0;
  left: 0;
  width: 100%;
  color: #ffffff;
  padding: 40px 0 15px;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__left {
    display: flex;
    align-items: center;
  }
  &__right {
    display: flex;
    align-items: center;
    margin-left: 30px;
  }
  &__logo {
    display: flex;
    align-items: center;
    &-icon {
      flex-shrink: 1;
      width: 40px;
      img {
        width: 100%;
      }
    }
    &-text {
      margin-left: 15px;
      p:nth-child(1) {
        font-size: 14px;
        line-height: 120%;
      }
      p:nth-child(2) {
        font-size: 25px;
        line-height: 120%;
        font-weight: 700;
        span {
          color: #B3FEFF;
        }
      }
    }
  }
  &__mail {
    display: flex;
    align-items: center;
    margin-left: 100px;
    &:hover {
      i {
        border: 1px solid #B3FEFF;
      }
      i svg path {
        fill: #B3FEFF;
      }
      span {
        color: #B3FEFF;
      }
    }
    i {
      width: 38px;
      height: 38px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: transparent;
      border: 1px solid rgba(255, 255, 255, 0.30);
      border-radius: 50%;
      transition: all 0.3s;
      svg {
        width: 14px;
        height: 14px;
      }
      svg path {
        transition: all 0.3s;
      }
    }
    span {
      font-size: 16px;
      line-height: 120%;
      margin-left: 15px;
      transition: all 0.3s;
    }
  }
  &__numbers {
    display: flex;
    align-items: center;
  }
  &__number {
    font-size: 16px;
    line-height: 120%;
    font-weight: 500;
    &:not(:first-child) {
      margin-left: 15px;
    }
    &:hover {
      color: #B3FEFF;
    }
  }
  &__burger {
    width: 38px;
    height: 38px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid rgba(255, 255, 255, 0.30);
    border-radius: 50%;
    cursor: pointer;
    margin-left: 30px;
    transition: all 0.3s;
    svg {
      width: 17px;
      height: 13px;
      transition: all 0.3s;
    }
    &:hover {
      border-color: #B3FEFF;
      svg rect {
        fill: #B3FEFF;
      }
    }
  }
}

@media screen and (max-width: 1200px) {
  .header {
    &__mail {
      margin-left: 30px;
    }
    &__numbers {
      flex-direction: column;
    }
    &__number {
      &:not(:first-child) {
        margin: 10px 0 0 0;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .header {
    padding: 30px 0 15px;
    &__logo {
      &-text {
        display: none;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .header {
    &__right {
      margin-left: 15px;
    }
    &__mail {
      margin-left: 15px;
      i {
        width: 30px;
        height: 30px;
      }
      span {
        font-size: 14px;
        margin-left: 8px;
      }
    }
    &__number {
      font-size: 14px;
    }
    &__burger {
      width: 30px;
      height: 30px;
      margin-left: 15px;
      svg {
        width: 14px;
        height: 11px;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .header {
    &__logo {
      &-icon {
        width: 30px;
      }
    }
    &__mail {
      span {
        display: none;
      }
    }
  }
}